let globalData = getApp().globalData;
let utils = require('@/util/utils.js');
let request = utils.api;
export default {
	data() {
		return {
			username: "",
			password: "",
			version: globalData.system.version,
			systemName: globalData.system.systemName,
			loading: false
		}
	},
	methods: {
		login: function() {
			if (!uni.getStorageSync('userinfo')) {
				uni.showLoading({
					title: "正在登录,请稍后",
					mask: false
				})
			}
			this.loading = false;
			let _this = this;
			uni.login().then(data => {
				let [err, res] = data;
				console.log("微信自动登录：", err, res, globalData.system.appId);
				if (err) {
					uni.hideLoading();
					_this.loading = true;
					uni.showToast({
						icon: "none",
						duration: 2000,
						title: "微信登录失败,请检查网络"
					})
					return;
				}
				console.log(request)
				request.getOpenId({
						code: res.code,
						appId: globalData.system.appId
					})
					.then(data => {
						let [err, res] = data;
						let openId = res.data.data.openId;
						globalData.openId = openId;
						globalData.serverTime = res.data.data.serverTime;
						console.log("openId解析返回信息：", err, res);
						if (err == null && res.data.data) {
							request.loginOpenid({
									openId: globalData.openId
								})
								.then(data => {
									let [err, res] = data;
									console.log("openid登录", err, res)
									uni.hideLoading();
									if (err == null && res.data.success) {
										globalData.userinfo = res.data.data.userinfo;
										utils.getModelList(globalData.userinfo.usernumber, this);

										// 存入storage
										uni.setStorageSync("userinfo", res.data.data.userinfo);
										uni.redirectTo({
											url: '../index/index'
										});
										_this.loading = true;
									}else{
										_this.loading = true;
									}
								})
						} else {
							uni.hideLoading()
							_this.loading = true;
						}
					});
			})
		},
		loginUser: function() {
			let _this = this;
			console.log("手动登录信息:", this.username, this.password, globalData.openId)
			uni.showLoading({
				title: "正在登录,请稍后",
				mask: false
			})
			if (globalData.openId) {
				this.requestLogin()
			} else {
				uni.login().then(data => {
					let [err, res] = data;
					console.log("微信登录：", err, res, globalData.system.appId)
					if (err) {
						uni.hideLoading();
						uni.showToast({
							icon: "none",
							duration: 2000,
							title: "微信登录失败:" + err.errMsg
						})
						return;
					}
					request.getOpenId({
							code: res.code,
							appId: globalData.system.appId
						})
						.then(data => {
							let [err, res] = data;
							console.log("openId解析返回信息：", err, res);
							if (err == null && res.data.data) {
								let openId = res.data.data.openId;
								globalData.openId = openId;
								globalData.serverTime = res.data.data.serverTime;
								console.log("登录中...", res);
								// 存入storage
								globalData.userinfo = res.data.data.userinfo;
								utils.getModelList(globalData.userinfo.usernumber, this);
								uni.setStorageSync("userinfo", res.data.data.userinfo);
								this.requestLogin();
							} else {
								uni.hideLoading();
								uni.showToast({
									icon: "none",
									duration: 2000,
									title: "登录失败:" + err.errMsg
								})
							}
						})
				})
			}
		},
		requestLogin: function() {
			let _this = this;
			// console.log('手动登录信息',_this.username,_this.password,_this.uni.getStorageSync('openid'))
			request.login({
					loginid: _this.username,
					pwd: _this.password,
					openid: globalData.openId
				})
				.then(data => {
					let [err, res] = data;
					console.log("登录信息:", err, res);
					uni.hideLoading();
					if (err) {
						uni.showToast({
							icon: "none",
							duration: 3000,
							title: "登录失败:" + res.data.errmsg
						})
					}
					if (res.data.success) {
						console.log("登录成功", res.data)
						globalData.userinfo = res.data.data.userinfo;
						// 存入storage
						uni.setStorageSync("userinfo", res.data.data.userinfo);
						utils.getModelList(globalData.userinfo.usernumber, this);
						uni.redirectTo({
							url: '../index/index'
						});
					} else {
						uni.showToast({
							icon: "none",
							duration: 3000,
							title: "登录失败:" + res.data.errmsg
						})
					}
				})
		}
	},
	onShow() {
		// if(uni.getStorageSync('userinfo')){

		// globalData.userinfo = uni.getStorageSync('userinfo');
		// utils.getModelList(globalData.userinfo.usernumber,this);
		// uni.redirectTo({
		// 	url: '../index/index'
		// });
		// return;
		// }
		// 静默登录
		this.login().then(() => {
			utils.getModelList(globalData.userinfo.usernumber, this);
		});

	},
	// 分享
	onShareAppMessage() {
		return {
			path: 'user/login/login'
		}
	}
}
